body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100vh;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.Navbar {
  /* opacity: 0;
  animation: 1s fadeIn 0.5s forwards; */
}

h1 {
  margin-top: 40px;
}

.logo {
  font-size: 5.5em;
  margin-bottom: 15px;
  font-family: 'Luckiest Guy', cursive;
}

.tag {
  font-style: italic;
}

.header {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
}

/* @keyframes fadeIn {
  100% {
    opacity: 1
  }
} */

hr {
  width: 30%;
  height: 3px;
  background-color: #3f51b5;
  border: 0;
}

.Navbar .header i {
  margin: auto;
  position: relative;
  top: 30px;
  left: -110px;
  -webkit-transform: rotate(25deg);
          transform: rotate(25deg);
}
.Footer {
  padding-top: 20px;
  /* opacity: 0; */
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  /* animation: 1s fadeIn 0.5s forwards; */
  margin-bottom: 20px;
}

.random-button {
  padding: 20px 40px;
  background-color: #3d3d3d;
  border: none;
  border-radius: 7px;
  color: white;
  margin-bottom: 20px;
  font-size: 20px;
}

.view-all-button {
  padding: 10px 20px;
  background-color: #3d3d3d;
  border: none;
  border-radius: 7px;
  color: white;
  margin-bottom: 40px;
  font-size: 12px;
}

button:hover {
  cursor: pointer;
}

a {
  text-decoration: none;
  color: #fff;
}

.Footer .contact {
  margin-bottom: 20px;
}

/* @keyframes fadeIn {
  100% {
    opacity: 1
  }
} */
.Main {
  opacity: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  min-height: 150px;
  padding: 30px;
  -webkit-animation: 1s fadeIn 0.5s forwards;
          animation: 1s fadeIn 0.5s forwards;
}

.story {
  line-height: 2em;
  font-size: 1.5em;
}

@-webkit-keyframes fadeIn {
  100% {
    opacity: 1;
  }
}

@keyframes fadeIn {
  100% {
    opacity: 1;
  }
}
.all-quotes {
  line-height: 2em;
  font-size: 1.5em;
}

.quotes-content {
  margin: 100px 200px;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.tweet {
  margin-left: 200px;
}
.App {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  text-align: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  color: white;
  background-image: linear-gradient(to right top, #3f51b5, #3044ba, #2137bd, #1026c0, #000bc1);
  font-family: 'Roboto',
    sans-serif;
  min-height: 100vh;
}
